@charset "UTF-8";
body {
  margin: 0;
  padding: 0;
}

html {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}

@font-face {
  font-family: "Montserrat Alternates";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/MontserratAlternates-Regular.woff2") format("woff2"), url("../fonts/MontserratAlternates-Regular.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "Montserrat Alternates";
  font-style: normal;
  font-weight: 700;
  src: url("../fonts/MontserratAlternates-Bold.woff2") format("woff2"), url("../fonts/MontserratAlternates-Bold.woff") format("woff");
  font-display: swap;
}
.page {
  font-family: "Montserrat Alternates", "Arial", sans-serif;
  font-weight: 400;
  font-size: 14px;
  color: #525453;
}
@media screen and (min-width: 768px) {
  .page {
    font-size: 16px;
  }
}
@media screen and (min-width: 1200px) {
  .page {
    font-size: 18px;
  }
}

.header {
  background-image: url("../img/mob/banner-mob.png");
  background-color: #ffffff;
  height: auto;
  width: 100%;
  background-repeat: no-repeat;
  background-position: center;
  padding: 16% 6% 0;
  box-sizing: border-box;
  position: relative;
  min-height: 400px;
}
@media (min-resolution: 2dppx) {
  .header {
    background-image: url("../img/mob/banner-mob@2x.png");
    background-size: 432px auto;
    min-height: 198px;
  }
}
@media screen and (min-width: 768px) {
  .header {
    padding: 22% 6% 0;
    min-height: 324px;
    position: relative;
  }
}
@media screen and (min-width: 768px) and (min-resolution: 2dppx) {
  .header {
    background-image: url("../img/mob/banner-mob@2x.png");
    background-size: auto;
    min-height: 398px;
  }
}
@media screen and (min-width: 1200px) {
  .header {
    padding: 10% 6% 0;
    min-height: 400px;
  }
}

.header__btn {
  width: 40px;
  height: 50px;
  padding: 10px 0;
  background-color: transparent;
  border: none;
  display: block;
  margin-left: auto;
  margin-right: 10%;
  color: #ffffff;
  font-family: inherit;
  font-size: 11px;
}

.header__title {
  color: #ffffff;
  font-weight: inherit;
  margin-top: 40px;
  margin-left: 8%;
  font-size: 26px;
  line-height: 30px;
}
@media screen and (min-width: 768px) {
  .header__title {
    font-size: 33px;
    position: absolute;
    top: 50%;
    left: 18%;
    transform: translate(-50%, -32%);
  }
}
@media screen and (min-width: 1200px) {
  .header__title {
    margin-left: 22%;
    top: 50%;
    left: 18%;
    transform: translate(-50%, -32%);
  }
}

.header::after {
  content: "";
  position: absolute;
  width: 395px;
  height: 20px;
  background-image: url("../img/mob/Line1.svg");
  background-repeat: no-repeat;
  bottom: -1%;
  left: 50%;
  transform: translate(-50%, -13%);
}
@media screen and (min-width: 1200px) {
  .header::after {
    background-image: url("../img/Line1-desc.svg");
    width: 1095px;
    height: 52px;
    bottom: -5%;
    left: 54%;
  }
}

.nav {
  display: none;
}
@media screen and (min-width: 1200px) {
  .nav {
    display: block;
    margin: 20px;
  }
}

@media screen and (min-width: 1200px) {
  .nav-list {
    padding: 0;
    margin: 0;
    display: flex;
    list-style-type: none;
    align-items: center;
    justify-content: center;
    gap: 0 10px;
  }
}

@media screen and (min-width: 1200px) {
  .nav-link {
    color: inherit;
    text-decoration: none;
    text-transform: uppercase;
  }
}

@media screen and (min-width: 1200px) {
  .nav-item {
    border: 2px solid #FFA8A8;
    padding: 5px 10px;
    border-radius: 14px;
    box-shadow: 7px 5px 18px -6px rgb(82, 84, 83);
    -webkit-box-shadow: 7px 5px 18px -6px rgb(82, 84, 83);
    -moz-box-shadow: 7px 5px 18px -6px rgb(82, 84, 83);
  }
}

@media screen and (min-width: 1200px) {
  .nav-link--form {
    color: #ffffff;
    background-color: #FFA8A8;
    font-size: 20px;
  }
}

@media screen and (min-width: 1200px) {
  .nav-item--form {
    background-color: #FFA8A8;
  }
}

.contact {
  width: 44%;
  font-size: 12px;
  display: flex;
  justify-content: end;
  align-items: end;
}

.contact__block > p {
  margin: 0;
}

@media screen and (min-width: 768px) {
  .contact__block {
    width: 180px;
  }
}
@media screen and (min-width: 1200px) {
  .contact__block {
    width: 120px;
  }
}

.contact__tg {
  color: #525453;
}

.contact__tg-icon {
  width: 61px;
}
@media screen and (min-width: 768px) {
  .contact__tg-icon {
    width: 35px;
  }
}

.content__text {
  display: block;
}

.content__text:not(:last-child) {
  margin-bottom: 10px;
}

.footer {
  background-color: #FFEFEF;
  padding: 6px 20px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
@media screen and (min-width: 768px) {
  .footer {
    padding: 6px 80px;
  }
}
@media screen and (min-width: 1200px) {
  .footer {
    padding: 1% 10%;
  }
}

.footer-block {
  display: flex;
  max-width: 450px;
  justify-content: space-between;
  width: 100%;
  align-items: center;
}
@media screen and (min-width: 768px) {
  .footer-block {
    max-width: 768px;
  }
}
@media screen and (min-width: 1200px) {
  .footer-block {
    max-width: 1200px;
  }
}

.footer__signature {
  width: 152px;
  font-size: 12px;
  position: relative;
}

.footer__signature::after {
  content: "";
  position: absolute;
  width: 12px;
  height: 12px;
  background-image: url("../img/mob/heart.png");
  background-repeat: no-repeat;
  top: 58%;
  right: 27%;
}

.form {
  position: relative;
  text-transform: uppercase;
  padding: 20px 60px;
}
@media screen and (min-width: 768px) {
  .form {
    padding: 5% 30%;
  }
}
@media screen and (min-width: 1200px) {
  .form {
    padding: 5% 33%;
  }
}

.form::before {
  content: "";
  position: absolute;
  width: 280px;
  height: 30px;
  background-image: url("../img/mob/Line2.svg");
  background-repeat: no-repeat;
  top: -8%;
  left: 0;
}
@media screen and (min-width: 768px) {
  .form::before {
    left: 18%;
    top: 0;
  }
}
@media screen and (min-width: 1200px) {
  .form::before {
    left: 22%;
  }
}

.form::after {
  content: "";
  position: absolute;
  width: 280px;
  height: 30px;
  background-image: url("../img/mob/Line3.svg");
  background-repeat: no-repeat;
  bottom: -26%;
  right: 4%;
}
@media screen and (min-width: 768px) {
  .form::after {
    right: 18%;
    bottom: -14%;
  }
}
@media screen and (min-width: 1200px) {
  .form::after {
    right: 22%;
  }
}

.form__btn {
  position: relative;
  background-color: #FFA8A8;
  display: block;
  width: 202px;
  height: 42px;
  color: #ffffff;
  padding: 8px 48px;
  font-size: 20px;
  margin-left: auto;
  text-decoration: none;
  border: 1px solid #ffa8a8;
  border-radius: 15px;
  box-shadow: 7px 7px 22px -6px rgb(82, 84, 83);
  -webkit-box-shadow: 7px 7px 22px -6px rgb(82, 84, 83);
  -moz-box-shadow: 7px 7px 22px -6px rgb(82, 84, 83);
}
@media screen and (min-width: 1200px) {
  .form__btn {
    width: 262px;
    height: 62px;
    font-size: 30px;
    padding: 10px 48px;
  }
}

.form__btn::before {
  content: "";
  position: absolute;
  width: 105px;
  height: 50px;
  background-image: url("../img/mob/tyk.png");
  background-repeat: no-repeat;
  top: -55px;
  right: -24px;
}

.form__btn:hover {
  width: 190px;
  height: 38px;
  background-color: #e79090;
  font-size: 16px;
  padding: 8px 50px;
}

.format {
  position: relative;
  padding: 76px 0 20px;
}
@media screen and (min-width: 768px) {
  .format {
    padding: 76px 88px 20px;
  }
}

.format::before {
  content: "Какой формат?";
  position: absolute;
  color: #ffffff;
  padding: 10px 16px;
  font-size: 19px;
  font-weight: 400;
  width: 197px;
  height: 44px;
  background-image: url("../img/mob/title-brown.svg");
  background-repeat: no-repeat;
  top: 15%;
