}
@media screen and (min-width: 1200px) {
  .format::before {
    background-image: url("../img/title-br2-desk.svg");
    width: 380px;
    height: 85px;
    padding: 18px 30px;
    font-size: 34px;
    text-align: center;
  }
}

.format-block {
  display: flex;
  gap: 0 6%;
}
@media screen and (min-width: 768px) {
  .format-block {
    justify-content: center;
    align-items: center;
    gap: 0 10%;
  }
}
@media screen and (min-width: 1200px) {
  .format-block {
    margin-top: 30px;
  }
}

.format__content {
  width: 50%;
  font-size: 13px;
}
@media screen and (min-width: 768px) {
  .format__content {
    font-size: 14px;
  }
}
@media screen and (min-width: 1200px) {
  .format__content {
    font-size: 18px;
  }
}

@media screen and (min-width: 1200px) {
