  }
}

.howto {
  display: flex;
  padding: 0 20px;
  gap: 0 12%;
}

.howto__item {
  display: flex;
  align-items: center;
  gap: 0 4px;
}

.imgs {
  position: relative;
}

.imgs::before {
  content: "";
  position: absolute;
  width: 1px;
  height: 300px;
  background-color: #525453;
  top: 21%;
  left: 38%;
