}

.when {
  position: relative;
  padding: 50px 0 20px;
  margin-bottom: 28px;
}

.when::after {
  content: "Когда?";
  position: absolute;
  color: #ffffff;
  padding: 6px 52px;
  font-size: 24px;
  font-weight: 400;
  width: 195px;
  height: 42px;
  background-image: url("../img/mob/title-brown.svg");
  background-repeat: no-repeat;
  top: -1%;
  left: 50%;
  transform: translate(-50%, -13%);
}
@media screen and (min-width: 1200px) {
  .when::after {
    background-image: url("../img/title-brown-desk.svg");
