.where {
  position: relative;
  padding: 50px 0 20px;
  display: flex;

  @include tablet {
    padding: 50px 80px 20px;
    margin: 0 auto;
    display: flex;
    justify-content: space-around;
  }

  @include desktop {
    margin-top: 80px;
  }
}

.where::after {
  content:"Где?";
  position: absolute;
  color: $text-color-primary;
  padding: 6px 70px;
  font-size: 24px;
  font-weight: 400;
  width: 195px;
  height: 42px;
  background-image: url("../img/mob/title-pink.svg");
  background-repeat: no-repeat;
  top: -1%;
  left: 30%;
  transform: translate(-50%, -13%);

  @include desktop {
    background-image: url("../img/title-roze-desk.svg");
    width: 332px;
    height: 84px;
    padding: 18px 118px;
    font-size: 38px;
    top: -14%;
  }
}

.where__text {
  text-align: center;
  position: relative;
}

.where__content {
  padding: 0 20px;

  @include tablet {
    font-size: 14px;
    width: 50%;
  }
}

.where__content-name {
  display: block;
  margin-bottom: 10px;

  @include desktop {
    font-size: 22px;
  }
}

.where__map {
  width: 200px;
  height: 202px;

  @include desktop {
    width: 400px;
    height: 200px;
  }
}

.where__text:after {
  content:"";
  position: absolute;
  width: 228px;
  height: 92px;
  background-image: url("../img/mob/border-pink.svg");
  background-repeat: no-repeat;
  top: -1%;
  left: 50%;
  transform: translate(-50%, -13%);  
}

.where__content-info {
  font-size: 12px;

  @include tablet {
    font-size: 14px;
  }

  @include desktop {
    font-size: 18px;
  }
}

  .where__content-subtitle {
    @include desktop {
      font-size: 18px;
    }
  }

.where__map {
  @include tablet {
    width: 380px;
    height: 180px;
  }
}